import { css } from '@emotion/react';
import type { ForwardedRef } from 'react';
import { forwardRef, useCallback, useEffect, useState } from 'react';

import FullHeightBox from './FullHeightBox';

const sizedWrapper = (width: number) => css`
  width: ${width}px;
`;

interface FullScreenBoxProps {
  children: React.ReactNode;
}

const FullScreenBox = (
  { children, ...others }: FullScreenBoxProps,
  ref: ForwardedRef<HTMLDivElement>,
): JSX.Element => {
  const [isWide, setIsWide] = useState(false);
  const [width, setWidth] = useState(0);

  const onResize = useCallback(() => {
    const isWidthBigger = window.innerWidth > window.innerHeight;
    setIsWide(isWidthBigger);
    setWidth(isWidthBigger ? window.innerHeight : 0);
  }, []);
  useEffect(() => {
    window.addEventListener('resize', onResize);
    onResize();
    return () => {
      window.removeEventListener('resize', onResize);
    };
  }, [onResize]);

  return (
    <FullHeightBox css={isWide && sizedWrapper(width)} {...others} ref={ref}>
      {children}
    </FullHeightBox>
  );
};

export default forwardRef<HTMLDivElement, FullScreenBoxProps>(FullScreenBox);
