import { css } from '@emotion/react';
import type { ForwardedRef } from 'react';
import { forwardRef, useCallback, useEffect, useState } from 'react';

const wrapper = (height: number) => css`
  height: ${height ? `${height}px` : '100vh'};
`;

interface FullHeightBoxProps {
  children: React.ReactNode;
}
const FullHeightBox = (
  { children, ...others }: FullHeightBoxProps,
  ref: ForwardedRef<HTMLDivElement>,
): JSX.Element => {
  const [height, setHeight] = useState(0);

  const onResize = useCallback(() => {
    setHeight(window.innerHeight);
  }, []);
  useEffect(() => {
    window.addEventListener('resize', onResize);
    onResize();
    return () => {
      window.removeEventListener('resize', onResize);
    };
  }, [onResize]);
  useEffect(() => {
    setHeight(window.innerHeight);
  }, []);
  return (
    <div css={wrapper(height)} {...others} ref={ref}>
      {children}
    </div>
  );
};

export default forwardRef<HTMLDivElement, FullHeightBoxProps>(FullHeightBox);
